import axios from 'axios';
import qs from 'qs';

const BASE_URL = process.env.REACT_APP_BASE_URL || 'http://localhost:5000';

const serverReq = axios.create({
    baseURL: BASE_URL
    // timeout: 10000
    // headers: {'X-Custom-Header': 'foobar'}
});

const getUserList = async (searchString, filterType, page = 1, limit = 24) => {
    try {
        const response = await serverReq.get('/users?page=2&count=10', {
            // params: {
            //     searchString,
            //     filterType,
            //     page,
            //     limit
            // }
        });

        return await response.data;
    } catch (error) {
        console.log('error', error);
        throw error;
    }
};

const getRadioBtnPositions = async () => {
    try {
        const response = await serverReq.get('/positions');

        return await response.data;
    } catch (error) {
        console.log('error', error);
        throw error;
    }
};

export { getUserList, getRadioBtnPositions };
